import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import { Row, Card, message, Select, Spin, Form, Input } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { getStudyField, updateStudyField, createStudyField } from 'services/StudyField';
import { getListTrainingSystem } from 'services/TrainingSystem';

const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    code: '',
    labelInEnglish: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formSpcializeTraining' })
class FormSpecializePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      systemId: false,
      initialValue: {},
      loading: false,
      disableCode: false,
      listSelect: [],
    };
  }

  componentDidMount = () => {
    const { match } = this.props;
    this.getListData();

    if (match.params.id) {
      this.setState({ systemId: match.params.id, loading: true, disableCode: true });
      getStudyField(match.params.id)
        .then(res => {
          const initialValue = handleInitialValue(res.data);
          this.setState({ initialValue, loading: false });
        })
        .catch(error => {
          console.log('error', error);
          this.setState({ loading: false });
        });
    }
  };

  getListData = () => {
    getListTrainingSystem(1, false)
      .then(res => {
        console.log('res.data', res.data);

        this.setState({
          listSelect: res.data.items,
        });
      })
      .catch(error => {
        console.log('====>', error);
      });
  };

  handleSubmit = e => {
    const { match } = this.props;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (match.params.id) {
          updateStudyField(match.params.id, values)
            .then(() => {
              message.success('Chỉnh sửa hệ đào tạo thành công');
              this.props.history.push('/specaialize/list');
            })
            .catch(error => {
              console.log('res', error);
              // message.error(error.response.data.message);
            });
        } else {
          createStudyField(values)
            .then(() => {
              message.success('Thêm hệ đào tạo thành công');
              this.props.history.push('/specaialize/list');
            })
            .catch(error => {
              console.log('res', error.response);
              // message.error(error.response.data.message);
            });
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { systemId, loading, listSelect, disableCode } = this.state;
    return (
      <Card title={systemId ? 'Cập nhật ngành' : 'Thêm ngành'}>
        <Spin spinning={loading}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              {disableCode ? null : (
                <Form.Item hasFeedback label="Hệ đào tạo" {...formItemLayout}>
                  {getFieldDecorator('trainTypeId', {
                    initialValue: initialValue.trainTypeId,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(
                    <Select style={{ width: '100%' }} placeholder="Chọn hệ đào tạo">
                      {listSelect.length > 0
                        ? listSelect.map(item => <Option key={item.id}>{item.label}</Option>)
                        : null}
                    </Select>
                  )}
                </Form.Item>
              )}
              <Form.Item {...formItemLayout} label="Mã ngành" hasFeedback>
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input disabled={this.state.disableCode} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên ngành" hasFeedback>
                {getFieldDecorator('code', {
                  initialValue: initialValue.label,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item style={{ display: 'none' }} {...formItemLayout} label="" hasFeedback>
                {getFieldDecorator('sfId', {
                  initialValue: initialValue.sfId,
                })(<Input type="hidden" />)}
              </Form.Item>
              <Form.Item style={{ display: 'none' }} {...formItemLayout} label="" hasFeedback>
                {getFieldDecorator('trainTypeId', {
                  initialValue: initialValue.trainTypeId,
                })(<Input type="hidden" />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
                {getFieldDecorator('labelInEnglish', {
                  initialValue: initialValue.labelInEnglish,
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSpecializePage {...props} />
  </Page>
));



