import { request } from 'utils';

const prefix = '/api/categories';

export const getListTrainingSystem = (page = 1, IsPagingEnabled = true) =>
  request({
    prefix,
    url: '/TrainType',
    params: {
      page,
      IsPagingEnabled,
    },
    method: 'GET',
  });
export const getTrainingSystem = id =>
  request({
    prefix,
    url: `/TrainType/GetById?id=${id}`,
    method: 'GET',
  });
export const deleteTrainingSystem = id =>
  request({
    prefix,
    url: `/TrainType`,
    data: {
      id,
    },
    method: 'DELETE',
  });
export const updateTrainingSystem = (id, values) =>
  request({
    prefix,
    url: `/TrainType`,
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
      regularId: values.regularId || '',
    },
    method: 'PUT',
  });

export const createTrainingSystem = values =>
  request({
    prefix,
    url: '/TrainType',
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
      regularId: values.regularId || '1',
    },
    method: 'POST',
  });



