import { request } from 'utils';

const prefix = '/api/categories';

export const getListStudyField = (page = 1, IsPagingEnabled, hasSFId) =>
  request({
    prefix,
    url: '/StudyField',
    params: {
      page,
      IsPagingEnabled,
      hasSFId,
    },
    method: 'GET',
  });

export const getStudyFields = ({
  label,
  labelInEnglish,
  trainTypeId,
  facultyId,
  courseId,
  hasSFId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/StudyField`,
    method: 'GET',
    params: {
      label,
      labelInEnglish,
      trainTypeId,
      facultyId,
      courseId,
      hasSFId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getStudyField = id =>
  request({
    prefix,
    url: `/StudyField/GetById`,
    method: 'GET',
    params: { id },
  });
export const deleteStudyField = id =>
  request({
    prefix,
    url: `/StudyField`,
    method: 'DELETE',
    data: { id },
  });
export const updateStudyField = (id, values) =>
  request({
    prefix,
    url: `/StudyField`,
    data: {
      id,
      label: values.code,
      sfId: values.sfId,
      labelInEnglish: values.labelInEnglish || '',
      trainTypeId: values.trainTypeId || '',
    },
    method: 'PUT',
  });

export const createStudyField = values =>
  request({
    prefix,
    url: '/StudyField',
    data: {
      id: values.id,
      label: values.code,
      sfId: values.sfId,
      labelInEnglish: values.labelInEnglish || '',
      trainTypeId: '01',
    },
    method: 'POST',
  });



